.hero {
  background: linear-gradient(135deg, #1e3c72 0%, #2a5298 100%);
  color: white;
  padding: 100px 20px;
  text-align: center;
}

.hero__content h1 {
  font-size: clamp(1.75rem, 5vw, 3rem);
  margin-bottom: 20px;
  font-weight: 700;
}

.hero__subtitle {
  font-size: clamp(1rem, 2vw, 1.25rem);
  margin-bottom: 40px;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

@media (max-width: 480px) {
  .hero__content h1 {
    font-size: 1.75rem;
  }

  .hero__subtitle {
    font-size: 1rem;
  }
}

.hero__buttons {
  display: flex;
  gap: 20px;
  justify-content: center;
  flex-wrap: wrap;
}

